$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  display: flex;
  align-items: center;
}

.mt-quadro-02 {
  &__left {
    margin: 0 6px 0 0;
  }

  &__inner-container {
    padding: 12px;
    background-color: #e0eeed;
    flex: 1;
  }

  &__title {
    color: #9abec0;
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    font-size: 14px;
    line-height: 1;
    margin: 0;
    padding-bottom: 4px;
    letter-spacing: 1px;
  }

  &__text {
    color: #000;
    font-family: 'Noto Sans', sans-serif;
    font-weight: 500;
    font-size: 14px;
    margin: 0;
    padding: 0;
  }
}
