import { Component, h, Host } from '@stencil/core';

@Component({
  tag: 'mt-quadro-02',
  styleUrl: 'quadro-02.scss',
  scoped: true,
})
export class Quadro02 {
  render() {
    return (
      <Host class="mt-quadro-02">
        <div class="mt-quadro-02__left">
          <slot name="left"></slot>
        </div>
        <div class="mt-quadro-02__inner-container">
          <h3 class="mt-quadro-02__title">
            <slot name="title"></slot>
          </h3>
          <p class="mt-quadro-02__text">
            <slot name="text"></slot>
          </p>
        </div>
      </Host>
    );
  }
}
